import React, { memo } from 'react';
import Child from './Child';
import { v4 as uuidv4 } from 'uuid';
import { AppState } from '../../reducers';
import { connect } from 'react-redux';
import {
  Statuses,
  Dates,
  Headings,
  Recommendations,
} from '../../actions/constants';
import { Nodes } from '../types/Map-Data';

interface Children {
  children: string[];
  subchildren?: boolean;
  center?: boolean;
  left?: boolean;
  right?: boolean;

  status: Statuses;
  goalDates: Dates;
  headings: Headings;
  recommendations: Recommendations;
  data: Nodes;
}

function Children({
  children,
  status,
  goalDates,
  headings,
  recommendations,
  data,
  ...props
}: Children): JSX.Element | null {
  if (Array.isArray(children) && children.length === 0) return null;
  return (
    <div
      className={`section__side-elements ${
        props.left
          ? 'section__side-elements--left'
          : props.right
          ? 'section__side-elements--right'
          : ''
      } ${props.center ? 'section__side-elements--center-section' : ''} ${
        props.subchildren ? 'section__side-elements--subchild' : ''
      }`}
    >
      {children.map((child) => {
        return (
          <Child
            key={uuidv4()}
            child={child}
            subchildren={props.subchildren}
            center={props.center}
            left={props.left}
            right={props.right}
            status={status}
            goalDates={goalDates}
            headings={headings}
            recommendations={recommendations}
            data={data}
          />
        );
      })}
    </div>
  );
}

interface StateProps {
  status: Statuses;
  goalDates: Dates;
  headings: Headings;
  recommendations: Recommendations;
  data: Nodes;
}

const mapStateToProps = (state: AppState): StateProps => ({
  data: state.data,
  status: state.status,
  goalDates: state.goalDates,
  headings: state.headings,
  recommendations: state.recommendations,
});

export default connect(mapStateToProps)(Children);
